import { motion } from 'framer-motion';
import { useState } from 'react';

interface FormNavigationButtonProps {
  type: 'previous' | 'continue' | 'submit';
  onClick: () => void;
  disabled?: boolean;
  text?: string;
}

function FormNavigationButton({ type, onClick, disabled = false, text }: FormNavigationButtonProps) {
  const isPrevious = type === 'previous';
  const isSubmit = type === 'submit';
  
  const getButtonText = () => {
    if (text) return text;
    if (isSubmit) return 'Submit Application';
    return isPrevious ? 'Previous' : 'Continue';
  };

  const baseClasses = "flex items-center px-6 py-3 rounded-lg font-medium transition-all duration-200 border-2";
  
  const previousClasses = disabled 
    ? 'text-gray-400 border-gray-200 cursor-not-allowed bg-gray-100'
    : 'text-dblue border-dblue hover:bg-dblue hover:text-white bg-white shadow-sm hover:shadow-md';
    
  const continueClasses = 'bg-amber-400 text-dblue font-semibold hover:bg-amber-500 hover:text-white shadow-md hover:shadow-lg border-amber-400 hover:border-amber-500';

  const buttonClasses = isPrevious 
    ? `${baseClasses} ${previousClasses}`
    : `${baseClasses} ${continueClasses} px-8`;

  return (
    <motion.button
      onClick={onClick}
      disabled={disabled}
      className={buttonClasses}
      whileHover={!disabled ? { scale: 1.02 } : {}}
      whileTap={!disabled ? { scale: 0.98 } : {}}
    >
      {isPrevious && (
        <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
        </svg>
      )}
      <span>{getButtonText()}</span>
      {!isPrevious && (
        <svg className="w-4 h-4 ml-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
        </svg>
      )}
    </motion.button>
  );
}

interface StudentData {
  firstName: string;
  lastName: string;
  dateOfBirth: string;
  gender: 'female' | 'male' | '';
  homeAddress: string;
  city: string;
  zipCode: string;
}

interface ParentData {
  parentFirstName: string;
  parentLastName: string;
  parentEmail: string;
  parentPhone: string;
  workAddress: string;
  workPhone: string;
  relationship: string;
}

interface EmergencyContact {
  name: string;
  phone: string;
  secondaryPhone: string;
  email: string;
  relationship: string;
  occupation: string;
  employer: string;
}

interface ClassroomRequest {
  classroom: string;
  requestedDays: string;
  startDate: string;
  spacePreference: string;
}

interface MedicalInfo {
  physician: string;
  physicianPhone: string;
  allergies: string;
  medications: string;
  medicalConditions: string;
  immunizations: string;
}

interface ApplicationData {
  student: StudentData;
  parent: ParentData;
  classroomRequest: ClassroomRequest;
  emergencyContact1: EmergencyContact;
  emergencyContact2: EmergencyContact;
  medical: MedicalInfo;
  agreements: {
    parentContract: boolean;
    webConsent: boolean;
    photoConsent: boolean;
  };
}

export default function ApplicationForm() {
  const [currentStep, setCurrentStep] = useState(1);
  const [applicationData, setApplicationData] = useState<ApplicationData>({
    student: {
      firstName: '',
      lastName: '',
      dateOfBirth: '',
      gender: '',
      homeAddress: '',
      city: '',
      zipCode: ''
    },
    parent: {
      parentFirstName: '',
      parentLastName: '',
      parentEmail: '',
      parentPhone: '',
      workAddress: '',
      workPhone: '',
      relationship: ''
    },
    classroomRequest: {
      classroom: '',
      requestedDays: '',
      startDate: '',
      spacePreference: ''
    },
    emergencyContact1: {
      name: '',
      phone: '',
      secondaryPhone: '',
      email: '',
      relationship: '',
      occupation: '',
      employer: ''
    },
    emergencyContact2: {
      name: '',
      phone: '',
      secondaryPhone: '',
      email: '',
      relationship: '',
      occupation: '',
      employer: ''
    },
    medical: {
      physician: '',
      physicianPhone: '',
      allergies: '',
      medications: '',
      medicalConditions: '',
      immunizations: ''
    },
    agreements: {
      parentContract: false,
      webConsent: false,
      photoConsent: false
    }
  });

  const totalSteps = 4;
  const completionPercentage = ((currentStep - 1) / totalSteps) * 100;

  const handleInputChange = (section: keyof ApplicationData, field: string, value: string | boolean) => {
    setApplicationData(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: value
      }
    }));
  };

  const validateStep = (step: number): boolean => {
    switch (step) {
      case 1:
        const studentFields = ['firstName', 'lastName', 'dateOfBirth', 'gender', 'homeAddress', 'city', 'zipCode'];
        return studentFields.every(field => applicationData.student[field as keyof StudentData] !== '');
      case 2:
        const parentFields = ['parentFirstName', 'parentLastName', 'parentEmail', 'parentPhone', 'relationship'];
        return parentFields.every(field => applicationData.parent[field as keyof ParentData] !== '');
      case 3:
        const classroomFields = ['classroom', 'requestedDays', 'spacePreference'];
        return classroomFields.every(field => applicationData.classroomRequest[field as keyof ClassroomRequest] !== '');
      case 4:
        const emergencyFields = ['name', 'phone', 'relationship'];
        return emergencyFields.every(field => applicationData.emergencyContact1[field as keyof EmergencyContact] !== '');
      default:
        return true;
    }
  };

  const handleContinue = () => {
    if (validateStep(currentStep)) {
      if (currentStep === totalSteps) {
        // Submit form
        handleSubmit();
      } else {
        setCurrentStep(prev => Math.min(prev + 1, totalSteps));
      }
    } else {
      alert('Please fill in all required fields.');
    }
  };

  // Excel/CSV export functionality (backup)
  const exportToCSV = (data: ApplicationData) => {
    const csvRows = [];
    
    // Headers
    csvRows.push(['Field Category', 'Field Name', 'Value']);

    // Student Information
    Object.entries(data.student).forEach(([key, value]) => {
      csvRows.push(['Student Information', key, value]);
    });

    // Parent Information
    Object.entries(data.parent).forEach(([key, value]) => {
      csvRows.push(['Parent/Guardian Information', key, value]);
    });

    // Classroom Request
    Object.entries(data.classroomRequest).forEach(([key, value]) => {
      csvRows.push(['Classroom Request', key, value]);
    });

    // Emergency Contacts
    Object.entries(data.emergencyContact1).forEach(([key, value]) => {
      csvRows.push(['Emergency Contact 1', key, value]);
    });
    Object.entries(data.emergencyContact2).forEach(([key, value]) => {
      csvRows.push(['Emergency Contact 2', key, value]);
    });

    // Medical Information
    Object.entries(data.medical).forEach(([key, value]) => {
      csvRows.push(['Medical Information', key, value]);
    });

    // Agreements
    Object.entries(data.agreements).forEach(([key, value]) => {
      csvRows.push(['Agreements & Consent', key, value ? 'Yes' : 'No']);
    });

    // Convert to CSV string
    const csvContent = csvRows.map(row => 
      row.map(field => `"${field}"`).join(',')
    ).join('\n');

    // Create and download file
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    
    if (link.download !== undefined) {
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', `hcc-application-${data.student.firstName}-${data.student.lastName}-${new Date().toISOString().split('T')[0]}.csv`);
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const handleSubmit = async () => {
    try {
      // Send data to PHP backend
      const response = await fetch('/api/submit-application.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(applicationData)
      });

      const result = await response.json();

      if (response.ok && result.success) {
        alert('Application submitted successfully! You will receive a confirmation email shortly, and we will contact you within 2-3 business days.');
        
        // Optional: Also export to CSV as backup
        exportToCSV(applicationData);
        
        // Reset form or redirect
        // setCurrentStep(1);
        // setApplicationData(/* reset to initial state */);
      } else {
        throw new Error(result.error || 'Failed to submit application');
      }
    } catch (error) {
      console.error('Submission error:', error);
      alert('There was an error submitting your application. Please try again or contact us directly at 604.986.2388.');
      
      // Fallback: still export CSV so data isn't lost
      exportToCSV(applicationData);
    }
  };

  const handlePrevious = () => {
    setCurrentStep(prev => Math.max(prev - 1, 1));
  };

  return (
    <div className="min-h-screen bg-cover bg-center relative" 
         style={{ backgroundImage: "url('/images/kidsattable.jpg')" }}>
      {/* Background overlay */}
      <div className="absolute inset-0 bg-black/20"></div>
      
      {/* Content */}
      <div className="relative z-10 min-h-screen flex items-center justify-center p-6">
        <motion.div 
          className="w-full max-w-4xl"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          {/* Header */}
          <div className="text-center mb-8">
            <motion.div 
              className="bg-white rounded-2xl p-8 shadow-lg"
              initial={{ opacity: 0, scale: 0.95 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.5 }}
            >
              <h1 className="text-3xl md:text-4xl font-bold text-dblue mb-2">
                Harbourside Children's Center
              </h1>
              <h2 className="text-2xl md:text-3xl font-semibold text-dblue">
                Application Form
              </h2>
            </motion.div>
          </div>

          {/* Form Container */}
          <motion.div 
            className="bg-white rounded-2xl shadow-lg overflow-hidden"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
          >
            {/* Enhanced Progress Header */}
            <div className="p-6 border-b border-gray-200 bg-gradient-to-r from-gray-50 to-white">
              <div className="flex justify-between items-center mb-6">
                <div>
                  <span className="text-lg font-semibold text-dblue">
                    Step {currentStep} of {totalSteps}
                  </span>
                  <p className="text-sm text-gray-600 mt-1">
                    {currentStep === 1 && "Student Information"}
                    {currentStep === 2 && "Parent/Guardian Details"}
                    {currentStep === 3 && "Classroom Request"}
                  </p>
                </div>
                <div className="text-right">
                  <span className="text-2xl font-bold text-dblue">
                    {Math.round(completionPercentage)}%
                  </span>
                  <p className="text-sm text-gray-600">Completed</p>
                </div>
              </div>
              
              {/* Enhanced Progress Bar with Steps */}
              <div className="relative">
                {/* Background bar */}
                <div className="w-full bg-gray-200 rounded-full h-3 shadow-inner">
                  <motion.div 
                    className="bg-blue-600 h-3 rounded-full shadow-sm"
                    initial={{ width: 0 }}
                    animate={{ width: `${completionPercentage}%` }}
                    transition={{ duration: 0.8, ease: "easeInOut" }}
                  />
                </div>
                
                {/* Step indicators */}
                <div className="flex justify-between absolute top-0 w-full transform -translate-y-1">
                  {[1, 2, 3, 4, 5].map((step) => (
                    <div key={step} className="flex flex-col items-center">
                      <motion.div
                        className={`w-5 h-5 rounded-full border-2 flex items-center justify-center text-xs font-bold ${
                          step < currentStep
                            ? 'bg-green-300 border-green-300 text-white'
                            : step === currentStep
                            ? 'bg-orange-300 border-orange-300 text-black'
                            : 'bg-white border-gray-300 text-gray-400'
                        }`}
                        initial={{ scale: 0.8 }}
                        animate={{ 
                          scale: step === currentStep ? 1.2 : 1,
                          rotate: step < currentStep ? 360 : 0
                        }}
                        transition={{ duration: 0.3 }}
                      >
                        {step < currentStep ? (
                          <svg className="w-3 h-3 text-white" fill="currentColor" viewBox="0 0 20 20">
                            <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                          </svg>
                        ) : (
                          step
                        )}
                      </motion.div>
                      <span className={`text-xs mt-2 font-medium ${
                        step <= currentStep ? 'text-dblue' : 'text-gray-400'
                      }`}>
                        {step === 1 && "Student"}
                        {step === 2 && "Parent"}
                        {step === 3 && "Classroom"}
                        {step === 4 && "Emergency"}
                        {step === 5 && "Agreements"}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* Step Content */}
            <div className="p-6">
              {currentStep === 1 && (
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.4 }}
                >
                  {/* Section Header */}
                  <div className="flex items-center mb-6">
                    <div className="bg-amber-100 p-3 rounded-full mr-4">
                      <svg className="w-6 h-6 text-amber-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z" />
                      </svg>
                    </div>
                    <h3 className="text-xl font-semibold text-amber-600">
                      Student Information
                    </h3>
                  </div>

                  {/* Instructions */}
                  <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-8">
                    <p className="text-blue-700 text-sm">
                      Please provide accurate information about the student. Fields marked with an asterisk(*) are required.
                    </p>
                  </div>

                  {/* Form Fields */}
                  <div className="space-y-6">
                    {/* Name Fields */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          First Name*
                        </label>
                        <input
                          type="text"
                          value={applicationData.student.firstName}
                          onChange={(e) => handleInputChange('student', 'firstName', e.target.value)}
                          placeholder="Enter First Name"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-300 focus:border-amber-300 transition-colors"
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Last Name*
                        </label>
                        <input
                          type="text"
                          value={applicationData.student.lastName}
                          onChange={(e) => handleInputChange('student', 'lastName', e.target.value)}
                          placeholder="Enter last name"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-300 focus:border-amber-300 transition-colors"
                          required
                        />
                      </div>
                    </div>

                    {/* Date of Birth and Gender */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Date Of Birth*
                        </label>
                        <input
                          type="date"
                          value={applicationData.student.dateOfBirth}
                          onChange={(e) => handleInputChange('student', 'dateOfBirth', e.target.value)}
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-300 focus:border-amber-300 transition-colors"
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Gender*
                        </label>
                        <div className="flex gap-6 mt-3">
                          <label className="flex items-center">
                            <input
                              type="radio"
                              name="gender"
                              value="female"
                              checked={applicationData.student.gender === 'female'}
                              onChange={(e) => handleInputChange('student', 'gender', e.target.value)}
                              className="w-4 h-4 text-amber-600 border-gray-300 focus:ring-amber-300"
                            />
                            <span className="ml-2 text-sm text-gray-700">Female</span>
                          </label>
                          <label className="flex items-center">
                            <input
                              type="radio"
                              name="gender"
                              value="male"
                              checked={applicationData.student.gender === 'male'}
                              onChange={(e) => handleInputChange('student', 'gender', e.target.value)}
                              className="w-4 h-4 text-amber-600 border-gray-300 focus:ring-amber-300"
                            />
                            <span className="ml-2 text-sm text-gray-700">Male</span>
                          </label>
                        </div>
                      </div>
                    </div>

                    {/* Address */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Home Address*
                      </label>
                      <div className="relative">
                        <input
                          type="text"
                          value={applicationData.student.homeAddress}
                          onChange={(e) => handleInputChange('student', 'homeAddress', e.target.value)}
                          placeholder="Street Address"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-300 focus:border-amber-300 transition-colors pr-10"
                          required
                        />
                        <button
                          type="button"
                          className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600"
                        >
                          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                          </svg>
                        </button>
                      </div>
                    </div>

                    {/* City and Zip */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          City*
                        </label>
                        <input
                          type="text"
                          value={applicationData.student.city}
                          onChange={(e) => handleInputChange('student', 'city', e.target.value)}
                          placeholder="Enter City"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-300 focus:border-amber-300 transition-colors"
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Zip code*
                        </label>
                        <input
                          type="text"
                          value={applicationData.student.zipCode}
                          onChange={(e) => handleInputChange('student', 'zipCode', e.target.value)}
                          placeholder="Enter Zip Code"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-300 focus:border-amber-300 transition-colors"
                          required
                        />
                      </div>
                    </div>
                  </div>
                </motion.div>
              )}

              {/* Step 2: Parent Information */}
              {currentStep === 2 && (
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.4 }}
                >
                  <div className="flex items-center mb-6">
                    <div className="bg-blue-100 p-3 rounded-full mr-4">
                      <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                      </svg>
                    </div>
                    <h3 className="text-xl font-semibold text-blue-600">
                      Parent/Guardian Information
                    </h3>
                  </div>

                  <div className="space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          First Name*
                        </label>
                        <input
                          type="text"
                          value={applicationData.parent.parentFirstName}
                          onChange={(e) => handleInputChange('parent', 'parentFirstName', e.target.value)}
                          placeholder="Enter First Name"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-300 focus:border-blue-300 transition-colors"
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Last Name*
                        </label>
                        <input
                          type="text"
                          value={applicationData.parent.parentLastName}
                          onChange={(e) => handleInputChange('parent', 'parentLastName', e.target.value)}
                          placeholder="Enter Last Name"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-300 focus:border-blue-300 transition-colors"
                          required
                        />
                      </div>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Email*
                        </label>
                        <input
                          type="email"
                          value={applicationData.parent.parentEmail}
                          onChange={(e) => handleInputChange('parent', 'parentEmail', e.target.value)}
                          placeholder="Enter Email"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-300 focus:border-blue-300 transition-colors"
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Phone*
                        </label>
                        <input
                          type="tel"
                          value={applicationData.parent.parentPhone}
                          onChange={(e) => handleInputChange('parent', 'parentPhone', e.target.value)}
                          placeholder="Enter Phone"
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-300 focus:border-blue-300 transition-colors"
                          required
                        />
                      </div>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Relationship*
                      </label>
                      <select
                        value={applicationData.parent.relationship}
                        onChange={(e) => handleInputChange('parent', 'relationship', e.target.value)}
                        className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-300 focus:border-blue-300 transition-colors"
                        required
                      >
                        <option value="">Select Relationship</option>
                        <option value="mother">Mother</option>
                        <option value="father">Father</option>
                        <option value="guardian">Guardian</option>
                      </select>
                    </div>
                  </div>
                </motion.div>
              )}

              {/* Step 3: Classroom Request */}
              {currentStep === 3 && (
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.4 }}
                >
                  <div className="flex items-center mb-6">
                    <div className="bg-yellow-100 p-3 rounded-full mr-4">
                      <svg className="w-6 h-6 text-yellow-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                      </svg>
                    </div>
                    <h3 className="text-xl font-semibold text-yellow-600">
                      Classroom Request
                    </h3>
                  </div>

                  <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-8">
                    <p className="text-blue-700 text-sm">
                      Please select which classroom and days you are interested in.
                    </p>
                  </div>

                  <div className="space-y-6">
                    {/* Classroom Selection */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Classroom applying for*
                      </label>
                      <div className="relative">
                        <select
                          value={applicationData.classroomRequest.classroom}
                          onChange={(e) => handleInputChange('classroomRequest', 'classroom', e.target.value)}
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-yellow-300 focus:border-yellow-300 transition-colors appearance-none"
                          required
                        >
                          <option value="">Select Classroom</option>
                          <option value="infant">Starfish Room (6 Months - 24 months)</option>
                          <option value="toddler">Seal Room (2 - 3 years)</option>
                          <option value="preschool">Dolphin or Orca Room (3 - 5 years)</option>
                        </select>
                        <svg className="absolute right-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                        </svg>
                      </div>
                    </div>

                    {/* Requested Days */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-3">
                        Requested Days (Note: we typically offer care for 2, 3, or 5 full days only, take 4 days on special request)*
                      </label>
                      <div className="space-y-4">
                        <label className="flex items-center">
                          <input
                            type="radio"
                            name="requestedDays"
                            value="2-days-mon-tue"
                            checked={applicationData.classroomRequest.requestedDays === '2-days-mon-tue'}
                            onChange={(e) => handleInputChange('classroomRequest', 'requestedDays', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300"
                          />
                          <span className="ml-3 text-sm text-gray-700">2 days (Monday, Tuesday)</span>
                        </label>
                        <label className="flex items-center">
                          <input
                            type="radio"
                            name="requestedDays"
                            value="2-days-thu-fri"
                            checked={applicationData.classroomRequest.requestedDays === '2-days-thu-fri'}
                            onChange={(e) => handleInputChange('classroomRequest', 'requestedDays', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300"
                          />
                          <span className="ml-3 text-sm text-gray-700">2 days (Thursday, Friday)</span>
                        </label>
                        <label className="flex items-center">
                          <input
                            type="radio"
                            name="requestedDays"
                            value="3-days-wed-thu-fri"
                            checked={applicationData.classroomRequest.requestedDays === '3-days-wed-thu-fri'}
                            onChange={(e) => handleInputChange('classroomRequest', 'requestedDays', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300"
                          />
                          <span className="ml-3 text-sm text-gray-700">3 days (Wednesday, Thursday, Friday)</span>
                        </label>
                        <label className="flex items-center">
                          <input
                            type="radio"
                            name="requestedDays"
                            value="3-days-mon-tue-wed"
                            checked={applicationData.classroomRequest.requestedDays === '3-days-mon-tue-wed'}
                            onChange={(e) => handleInputChange('classroomRequest', 'requestedDays', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300"
                          />
                          <span className="ml-3 text-sm text-gray-700">3 days (Monday, Tuesday, Wednesday)</span>
                        </label>
                        <label className="flex items-center">
                          <input
                            type="radio"
                            name="requestedDays"
                            value="5-days-mon-fri"
                            checked={applicationData.classroomRequest.requestedDays === '5-days-mon-fri'}
                            onChange={(e) => handleInputChange('classroomRequest', 'requestedDays', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300"
                          />
                          <span className="ml-3 text-sm text-gray-700">5 days (Monday-Friday)</span>
                        </label>
                      </div>
                    </div>

                    {/* Requested Start Date */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Requested Start Date
                      </label>
                      <input
                        type="date"
                        value={applicationData.classroomRequest.startDate}
                        onChange={(e) => handleInputChange('classroomRequest', 'startDate', e.target.value)}
                        className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-yellow-300 focus:border-yellow-300 transition-colors"
                      />
                    </div>

                    {/* Space Preference */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-3">
                        Preference of Space for Full-Time Applicants*
                      </label>
                      <div className="space-y-3">
                        <label className="flex items-start">
                          <input
                            type="radio"
                            name="spacePreference"
                            value="flexible"
                            checked={applicationData.classroomRequest.spacePreference === 'flexible'}
                            onChange={(e) => handleInputChange('classroomRequest', 'spacePreference', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300 mt-1"
                          />
                          <span className="ml-3 text-sm text-gray-700">Yes, I am flexible in taking 2 or 3 days until a full-time space becomes available.</span>
                        </label>
                        <label className="flex items-start">
                          <input
                            type="radio"
                            name="spacePreference"
                            value="wait-fulltime"
                            checked={applicationData.classroomRequest.spacePreference === 'wait-fulltime'}
                            onChange={(e) => handleInputChange('classroomRequest', 'spacePreference', e.target.value)}
                            className="w-4 h-4 text-yellow-600 border-gray-300 focus:ring-yellow-300 mt-1"
                          />
                          <span className="ml-3 text-sm text-gray-700">No, I will wait until a full-time space becomes available.</span>
                        </label>
                      </div>
                    </div>
                  </div>
                </motion.div>
              )}

              {/* Step 4: Emergency Contact Information */}
              {currentStep === 4 && (
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.4 }}
                >
                  <div className="flex items-center mb-6">
                    <div className="bg-red-100 p-3 rounded-full mr-4">
                      <svg className="w-6 h-6 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
                      </svg>
                    </div>
                    <h3 className="text-xl font-semibold text-red-600">
                      Emergency Contact Information
                    </h3>
                  </div>

                  <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-8">
                    <p className="text-blue-700 text-sm">
                      Please provide at least one emergency contact other than the Parents/guardians listed previously. This person will be contacted if we cannot reach the primary or secondary guardians.
                    </p>
                  </div>

                  <div className="space-y-8">
                    {/* Primary Emergency Contact */}
                    <div>
                      <h4 className="text-lg font-medium text-gray-700 mb-6">Primary Emergency Contact</h4>
                      <div className="space-y-6">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Full Name*
                            </label>
                            <input
                              type="text"
                              value={applicationData.emergencyContact1.name}
                              onChange={(e) => handleInputChange('emergencyContact1', 'name', e.target.value)}
                              placeholder="Enter First Name"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                              required
                            />
                          </div>
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Relationship with student*
                            </label>
                            <input
                              type="text"
                              value={applicationData.emergencyContact1.relationship}
                              onChange={(e) => handleInputChange('emergencyContact1', 'relationship', e.target.value)}
                              placeholder="e.g., Grandparent, Uncle, Family Friend"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                              required
                            />
                          </div>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Phone number*
                            </label>
                            <input
                              type="tel"
                              value={applicationData.emergencyContact1.phone}
                              onChange={(e) => handleInputChange('emergencyContact1', 'phone', e.target.value)}
                              placeholder="Enter Phone Number"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                              required
                            />
                          </div>
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Secondary Phone number*
                            </label>
                            <input
                              type="tel"
                              value={applicationData.emergencyContact1.secondaryPhone}
                              onChange={(e) => handleInputChange('emergencyContact1', 'secondaryPhone', e.target.value)}
                              placeholder="Enter Phone Number"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                            />
                          </div>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Email address*
                            </label>
                            <input
                              type="email"
                              value={applicationData.emergencyContact1.email}
                              onChange={(e) => handleInputChange('emergencyContact1', 'email', e.target.value)}
                              placeholder="Enter Email address"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                              required
                            />
                          </div>
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Employer
                            </label>
                            <input
                              type="text"
                              value={applicationData.emergencyContact1.employer}
                              onChange={(e) => handleInputChange('emergencyContact1', 'employer', e.target.value)}
                              placeholder="Enter Employer"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                            />
                          </div>
                        </div>

                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-2">
                            Occupation
                          </label>
                          <input
                            type="text"
                            value={applicationData.emergencyContact1.occupation}
                            onChange={(e) => handleInputChange('emergencyContact1', 'occupation', e.target.value)}
                            placeholder="Enter occupation"
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                          />
                        </div>
                      </div>
                    </div>

                    {/* Additional Emergency Contact */}
                    <div>
                      <h4 className="text-lg font-medium text-gray-700 mb-6">Additional Emergency Contact (Optional)</h4>
                      <div className="space-y-6">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Full Name*
                            </label>
                            <input
                              type="text"
                              value={applicationData.emergencyContact2.name}
                              onChange={(e) => handleInputChange('emergencyContact2', 'name', e.target.value)}
                              placeholder="Enter First Name"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                            />
                          </div>
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Relationship with student*
                            </label>
                            <input
                              type="text"
                              value={applicationData.emergencyContact2.relationship}
                              onChange={(e) => handleInputChange('emergencyContact2', 'relationship', e.target.value)}
                              placeholder="e.g., Grandparent, Uncle, Family Friend"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                            />
                          </div>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Phone number*
                            </label>
                            <input
                              type="tel"
                              value={applicationData.emergencyContact2.phone}
                              onChange={(e) => handleInputChange('emergencyContact2', 'phone', e.target.value)}
                              placeholder="(91)798465123"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                            />
                          </div>
                          <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                              Secondary Phone number*
                            </label>
                            <input
                              type="tel"
                              value={applicationData.emergencyContact2.secondaryPhone}
                              onChange={(e) => handleInputChange('emergencyContact2', 'secondaryPhone', e.target.value)}
                              placeholder="(91)798465123"
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-300 focus:border-red-300 transition-colors"
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </motion.div>
              )}

              {/* Other steps placeholder */}
              {currentStep > 4 && (
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.4 }}
                  className="text-center py-12"
                >
                  <h3 className="text-xl font-semibold text-gray-600 mb-4">
                    Step {currentStep} Content
                  </h3>
                  <p className="text-gray-500">
                    Additional steps are being developed.
                  </p>
                </motion.div>
              )}
            </div>

            {/* Navigation */}
            <div className="px-6 py-4 bg-gray-50 border-t border-gray-200 flex justify-between">
              <FormNavigationButton
                type="previous"
                onClick={handlePrevious}
                disabled={currentStep === 1}
              />

              <FormNavigationButton
                type={currentStep === totalSteps ? 'submit' : 'continue'}
                onClick={handleContinue}
              />
            </div>
          </motion.div>
        </motion.div>
      </div>
    </div>
  );
}
