import { motion } from 'framer-motion';

interface EducatorCardProps {
  name: string;
  title: string;
  room: string;
  delay?: number;
  placeholderType?: 'teacher' | 'graduate' | 'coming-soon';
  borderColor?: string;
  particleColor?: string;
  placeholderBgGradient?: string;
}

export default function EducatorCard({
  name,
  title,
  room,
  delay = 0,
  placeholderType = 'teacher',
  borderColor = 'border-yellow-200',
  particleColor = 'bg-yellow-300',
  placeholderBgGradient = 'from-blue-100 to-blue-200'
}: EducatorCardProps) {

  const getPlaceholderContent = () => {
    switch (placeholderType) {
      case 'teacher':
        return {
          emoji: '',
          emojiColor: 'text-blue-300',
          label: 'Photo Coming Soon'
        };
      case 'graduate':
        return {
          emoji: '',
          emojiColor: 'text-purple-300',
          label: 'Photo Coming Soon'
        };
      case 'coming-soon':
        return {
          emoji: '',
          emojiColor: 'text-yellow-500',
          label: 'Photo Coming Soon'
        };
      default:
        return {
          emoji: '',
          emojiColor: 'text-blue-300',
          label: 'Photo Coming Soon'
        };
    }
  };

  const placeholder = getPlaceholderContent();

  return (
    <motion.div 
      className="text-center relative"
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, delay }}
    >
      {/* PhotoWithBackdrop style effects */}
      <motion.div 
        className={`absolute -top-2 -left-2 w-full h-full border border-dashed ${borderColor} rounded-lg opacity-30`}
        animate={{ 
          rotate: [0, 1, -1, 0],
          scale: [1, 1.01, 1]
        }}
        transition={{ 
          duration: 4,
          repeat: Infinity,
          ease: "easeInOut",
          delay: delay * 2
        }}
      />
      
      <motion.div
        className={`absolute -top-1 -right-1 w-2 h-2 rounded-full ${particleColor}`}
        animate={{ 
          y: [0, -8, 0],
          opacity: [0.5, 1, 0.5]
        }}
        transition={{ 
          duration: 2,
          repeat: Infinity,
          ease: "easeInOut",
          delay: delay * 0.5
        }}
      />

      <div className="relative z-10 bg-white p-6 rounded-lg shadow-md hover:shadow-lg transition-shadow">
        {/* Placeholder Image */}
        <div className="w-48 h-48 mx-auto mb-4 rounded-lg relative overflow-hidden">
          <motion.img
            src="/images/Photo-Coming-Soon-1.png"
            alt="Photo Coming Soon"
            className="w-full h-full object-cover rounded-lg"
            whileHover={{ 
              scale: 1.05,
              transition: { duration: 0.3 }
            }}
            animate={{
              opacity: [0.8, 1, 0.8]
            }}
            transition={{
              duration: 2,
              repeat: Infinity,
              ease: "easeInOut"
            }}
          />
        </div>

        {/* Room and Star */}
        <div className="flex justify-center mb-2">
          <span className="text-yellow-400 text-lg"></span>
          <span className="text-dblue font-semibold ml-2">{room}</span>
        </div>

        {/* Title and Name */}
        <h3 className="text-lg font-bold text-dblue mb-1">{title}</h3>
        <p className="text-gray-600">{name}</p>
      </div>
    </motion.div>
  );
}
